// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import 'element-ui/lib/theme-chalk/index.css'//element
import ElementUI from 'element-ui'//element
import echarts from 'echarts'//echarts
import './common/axios'//axios
import BaiduMap from 'vue-baidu-map'//
Vue.prototype.$echarts = echarts ////echarts
Vue.use(ElementUI)
Vue.use(BaiduMap, { ak:'jKmWGS7erqnfrv8HfH4VGg72u5ipBIbT'    //ak
})
Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



