<template>
<div class="main">
<!---->
<el-row type="flex" justify="center" style="padding-top:30px;">
  <el-col :span="8">
<el-input v-model="keyword" prefix-icon="el-icon-search" /></el-col>
</el-row>
<el-row type="flex" justify="center" style="margin-top:20px;">
  <el-col :span="22">

 <!-- getPoint-->
 <!--scroll-wheel-zoom-->
<baidu-map class="map" id="mapID" center="" :zoom="zoom" :scroll-wheel-zoom="true" :continuous-zoom="true" :pinch-to-zoom="true" :auto-resize="true" @click="getPoint" >
    <!-- -->
    <bm-geolocation anchor="BMAP_ANCHOR_BOTTOM_RIGHT" :showAddressBar="true" :autoLocation="true"></bm-geolocation>
  <!---->
  <bm-map-type :map-types="['BMAP_NORMAL_MAP', 'BMAP_HYBRID_MAP']" anchor="BMAP_ANCHOR_TOP_LEFT"></bm-map-type>
  <!--input-->
  <!--display: none-->
  <!--zoom12.8-->
  <bm-local-search :keyword="keyword" :auto-viewport="true" :location="location" zoom="12.8" style="display: none"></bm-local-search>
  <bm-navigation anchor="BMAP_ANCHOR_TOP_RIGHT"></bm-navigation>
  <!--showinfoWindowCloseinfoWindowOpen-->
  <bm-marker :position="postionMap">
    <bm-info-window :show="show" @close="infoWindowClose" @open="infoWindowOpen">
      <!-- <p>{{ add.siteName }}</p> -->
      {{ add.site }}
    </bm-info-window>
  </bm-marker>
</baidu-map>
</el-col>
</el-row>
<!-- <p>{{postionMap.lat}},{{postionMap.lng}}</p> -->
</div>
</template>

<script>
export default {
    data(){
        return{
            show:'',
            zoom:'',
            keyword:'',
            address_info:
            {     city:'',//
            address:'',//
            point:'',//
            },
       
            add:{
               siteName:''
               ,site:"", 
               jd:'',
               wd:''
            },
            postionMap:{
              lng:'',
                lat:''
            }

        }
    },
    methods:{
  getPoint(e){    //
    this.show = true;
    this.postionMap.lng = e.point.lng;     //  e.point.lng
    this.postionMap.lat = e.point.lat;     //  e.point.lat
    this.add.jd = e.point.lng;
    this.add.wd = e.point.lat;
    this.zoom = e.target.getZoom();
    this.address_info.point = e.point.lat+','+e.point.lng;
    
 
    let geocoder= new BMap.Geocoder();  //
    geocoder.getLocation(e.point,rs=>{
      this.add.site = rs.address;
      this.$prompt('','', {
          confirmButtonText: '',
          cancelButtonText: '',
          inputValue:rs.address,
          type:'info',
          center: true
        }).then(({value}) => {
            console.log(value)
            this.address_info.address = value;
            console.log("*******************")
          this.$message({
            type: 'success',
            message: ': ' + value
          });
          this.show = false;
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          }); 
          this.show = false;      
        });
      //(string)=
      // console.log(rs.address);    //
      // console.log(rs.addressComponents);//(object)
      // console.log(rs.addressComponents.province); //
      this.address_info.city = rs.addressComponents.city//
      // console.log(rs.addressComponents.district); //
      // console.log(rs.addressComponents.street); //
      // console.log(rs.addressComponents.streetNumber); //
      // console.log(rs.surroundingPois); //POI(array)
      // console.log(rs.business); //(string)
      this.$emit('sendparentPoint',{address_info:this.address_info})//
    });
  },
  infoWindowClose () {
    this.show = false
  },
  infoWindowOpen () {
    //
    //
    //100
  //  setInterval(()=>{
  //    this.show = true
  //  },100)
  },
}
}
</script>


<style scoped>
/* The container of BaiduMap must be set width & height. */
.main{
  background: rgb(240, 243, 243)
}
.map {
  width: 100%;
  height: 300px;
}
</style>



