<template>
  <div style="margin-top:0px;padding:0px;">
    <el-row type="flex" class="row-bg" justify="center">
      <el-col
        :span="16"
        style="border:1px solid #ebeef5;padding:30px;box-shadow: 10px 10px 5px #888888;"
      >
        <el-collapse accordion
        @change="select_id">
          <el-collapse-item>
            <template slot="title">
              <span style="float:left;">
                <i class="header-icon el-icon-info" style="color:#909399;"></i>
              </span>
              <span style="float:left;"></span>
            </template>
            <el-row type="flex" class="row-bg" justify="center">
            <el-col :span="14">
            <el-card style="width:100%;">
              <el-form :model="register" label-width="35%">
                <el-form-item label="">
                  <el-col :span="15">
                    <el-input v-model="register.email" autocomplete="off" placeholder=""></el-input>
                  </el-col>
                </el-form-item>
                <el-form-item label="">
                  <el-col :span="15">
                    <el-input v-model="register.compus_card" autocomplete="off" placeholder=""></el-input>
                  </el-col>
                </el-form-item>
                <el-form-item label="">
                  <el-col :span="15">
                    <el-input v-model="register.mobile" autocomplete="off" placeholder=""></el-input>
                  </el-col>
                </el-form-item>
                <el-button type="info" @click="update_form(register.id)" round> </el-button>
                
              </el-form>
            </el-card></el-col>
            <el-col :span="6" style="margin-left:10px;" >
            <el-card :body-style="{ padding: '0px',height:'100%', width:'100%', margin:'0px','line-height':'10px'}"
            v-if="JSON.stringify(ali_user)!='{}'">
              <div
                style="width:45px;height:45px;background:#E6A23C; line-height:40px; border-radius: 50%;"
              >
                <span class="el-icon-info" style="width:25%;height:5%;"></span>
                <span style="font-size:20%;"></span>
              </div>
              <div>
                <el-tag type="info" style="width:70%;">{{ali_user.nick_name}}</el-tag>
              </div>
              <div>
                <el-tag type="info" style="width:70%;">id{{ali_user.userali_id}}</el-tag>
              </div> 
            </el-card>
            <el-button type="info" @click="register_form" round></el-button></el-col></el-row>
            
          </el-collapse-item>
          <el-collapse-item>
            <template slot="title">
              <span style="float:left;">
                <i class="header-icon el-icon-info" style="color:#909399;"></i>
              </span>
              <span style="float:left;"></span>
            </template>
            <useraddress />
          </el-collapse-item>       
          <el-collapse-item>
            <template slot="title">
              <span style="float:left;">
                <i class="header-icon el-icon-info" style="color:#909399;"></i>
              </span>
              <span style="float:left;"></span>
            </template>
            <div></div>
         
          </el-collapse-item>
        </el-collapse>
      </el-col>
    </el-row>
    <!-- <el-row type="flex" class="row-bg" justify="center">
      <el-col :span="16">
        <el-collapse v-model="activeNames" @change="handleChange">
          <el-collapse-item title=" Consistency" name="1">
            <div></div>
            <div></div>
          </el-collapse-item>
          <el-collapse-item title=" Feedback" name="2">
            <div></div>
            <div></div>
          </el-collapse-item>
          <el-collapse-item title=" Efficiency" name="3">
            <div></div>
            <div></div>
            <div></div>
          </el-collapse-item>
          <el-collapse-item title=" Controllability" name="4">
            <div></div>
            <div></div>
          </el-collapse-item>
        </el-collapse>
      </el-col>
    </el-row>-->
  </div>
</template>

<script>
import useraddress from './user_address'
export default {
  data() {
    return {
      register:{},
      ali_user:{},
      activeNames: ["1"],
      ali_user_url: "https://openauth.alipaydev.com/oauth2/publicAppAuthorize.htm?app_id=2016091700530193&scope=auth_user&redirect_uri=http://neverqaz.cn/ali_login/",
      register: {
        name: "",
        gender: "",
        email: "",
        compus_card: "",
        mobile: "",
        password: ""
        // birthday:'',
        // ali_user_url: "https://openauth.alipaydev.com/oauth2/publicAppAuthorize.htm?app_id=2016091700530193&scope=auth_user&redirect_uri=http://neverqaz.cn/ali_login/"
      },
    };
  },
 mounted(){
   this.load_data()
 },
    components:{
      useraddress,
    },
  methods: {
    update_form:function(id){
      let that = this;
      that.axios.patch('/users/'+id+'/',that.register).then(res=>{
        console.log('update**************')
        console.log(res.data)
           that.$message({
            type: 'success',
            message: ''
          });
      })
    },
    load_data:function(){
      let that =this;
      that.axios.get('/users/').then(res=>{
        console.log(res.data)
        that.register =res.data[0]
        
      })
    },
    handleChange(val) {
      console.log(val);
    },
    select_id:function(){
      this.load_data()
    },
    register_form() {
     window.open(this.ali_user_url,'_blank')

    }
  }
};
</script>

<style>
</style>



