<template>
  <div>
    <template v-if="[5,15,25,35].indexOf(status)>=0">
      <el-row type="flex" justify="center" style="margin-top:80px;">
        <el-col :span="4">
          <div class="demo3"></div>
        </el-col>
      </el-row>
    </template>
    <template v-else>
      <el-row type="flex" justify="center" style="margin-top:80px;">
        <el-col :span="1">
          <div class="demo2"></div>
        </el-col>
      </el-row>
    </template>
    <el-row type="flex" justify="center" style="margin-top:30px;">
      <el-col :span="status_type[status][3]">
        <h2>{{status_type[status][1]}}</h2>
      </el-col>
    </el-row>
    <el-row type="flex" justify="center" style="margin-top:10px;">
      <el-col :span="status_type[status][2]">
        <span>{{status_type[status][0]}}</span>
      </el-col>
    </el-row>
    <el-row type="flex" justify="center" style="margin-top:50px;">
      <el-col :span="18">
        <span style="font-size:14px;font-weight: bold;">: QUEE</span>
      </el-col>
    </el-row>
    <el-row type="flex" justify="center" style="margin-top:8px;">
      <el-col :span="4">
        <span style="font-size:12px;">ID33545325</span>
      </el-col>
      <el-col :span="6">
        <span style="font-size:12px;">fengxu</span>
      </el-col>
      <el-col :span="8">
        <span style="font-size:12px;">2018-10-9 12:23:45</span>
      </el-col>
    </el-row>
    <el-steps
      :active="status_type[status][5]"
      align-center
      :process-status="status_type[status][4]"
      finish-status="success"
    >
      <el-step title="" description v-if="status==5"></el-step>
      <el-step title="" description v-else></el-step>
      <el-step title="" description=""></el-step>
      <el-step title="" description></el-step>
      <el-step title="IT" description></el-step>
      <el-step title="" description icon="el-icon-loading" v-if="status==40"></el-step>
      <el-step title="" description v-else></el-step>
      <el-step title="" description icon="el-icon-loading" v-if="status==50"></el-step>
      <el-step title="" description v-else></el-step>
      <el-step title="" description></el-step>
    </el-steps>
    <el-row type="flex" justify="center" style="margin-top:30px;">
      <el-col :span="6">
        <el-button type="primary" size="small"></el-button>
        <el-button size="small"></el-button>
      </el-col>
    </el-row>
    <div class="h1">
      
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      status: 30,
      status_type: {
        10: ["", "", 6, 4, "", 1],
        5: ["", "", 5, 4, "error", 0],
        20: ["", "", 7, 4, "", 2],
        15: ["", "", 4, 4, "error", 1],
        30: ["IT", "", 8, 5, "", 3],
        25: ["", "", 5, 5, "error", 2],
        40: ["IT", "IT", 6, 5, "", 4],
        35: ["IT", "IT", 5, 5, "error", 3],
        50: ["", "", 5, 4, "", 5],
        60: ["", "", 3, 3, "", 7]
      }
    };
  }
};
</script>

<style>
.demo2 {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background: green;
  display: block;
  position: relative;
}
.demo2::after {
  content: "";
  height: 50%;
  width: 8%;
  border-radius: 30%;
  display: block;
  background: white;
  position: absolute;
  top: 25%;
  left: 50%;
  transform: rotate(45deg);
}
.demo2::before {
  content: "";
  height: 27%;
  width: 8%;
  border-radius: 30%;
  display: block;
  background: white;
  transform: rotate(-45deg);
  position: absolute;
  top: 45%;
  left: 25%;
}

.demo3 {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background: red;
  display: block;
  position: relative;
}
.demo3::after {
  content: "";
  height: 65%;
  width: 8%;
  border-radius: 30%;
  display: block;
  background: white;
  position: absolute;
  top: 15%;
  left: 45%;
  transform: rotate(45deg);
}
.demo3::before {
  content: "";
  height: 65%;
  width: 8%;
  border-radius: 30%;
  display: block;
  background: white;
  position: absolute;
  top: 15%;
  left: 45%;
  transform: rotate(-45deg);
}
.h1{
    width: 10vw;
    height: 10vh;
}
</style>



